"use client";

import React, { useState, useEffect } from "react";
import { io, Socket } from "socket.io-client";
import {
  Task,
  PomodoroState,
  ServerToClientEvents,
  ClientToServerEvents,
} from "@/types";
import { TaskList } from "./tasks/TaskList";
import { TaskItem } from "./tasks/TaskItem";
import { PomodoroTimer } from "./pomodoro/PomodoroTimer";

export const TaskDisplay: React.FC = () => {
  // State management
  const [tasks, setTasks] = useState<Task[]>([]);
  const [pomodoroState, setPomodoroState] = useState<PomodoroState>({
    isActive: false,
    timeLeft: 25 * 60,
    mode: "work",
    session: 1,
    startTime: null,
    initiatedBy: null,
    autoBreaks: true,
  });
  const [socket, setSocket] = useState<Socket<
    ServerToClientEvents,
    ClientToServerEvents
  > | null>(null);
  const [isMinimized, setIsMinimized] = useState(false);
  const [showCompleted, setShowCompleted] = useState(true);
  const [connectionStatus, setConnectionStatus] = useState<
    "connecting" | "connected" | "disconnected"
  >("connecting");

  // Initialize socket connection
  useEffect(() => {
    const newSocket = io("http://localhost:3001", {
      path: "/socket.io",
      transports: ["websocket", "polling"],
    });

    setSocket(newSocket);

    // Connection event handlers
    newSocket.on("connect", () => {
      console.log("‚úÖ Connected to server");
      setConnectionStatus("connected");
    });

    newSocket.on("disconnect", () => {
      console.log("‚ùå Disconnected from server");
      setConnectionStatus("disconnected");
    });

    // Task event handlers
    newSocket.on("tasksLoaded", (loadedTasks) => {
      setTasks(loadedTasks);
    });

    newSocket.on("taskAdded", (newTask) => {
      setTasks((prev) => [...prev, newTask]);
    });

    newSocket.on("taskUpdated", (updatedTask) => {
      setTasks((prev) =>
        prev.map((task) => (task.id === updatedTask.id ? updatedTask : task))
      );
    });

    newSocket.on("taskCompleted", (completedTask) => {
      setTasks((prev) =>
        prev.map((task) =>
          task.id === completedTask.id ? completedTask : task
        )
      );
    });

    newSocket.on("taskDeleted", (deletedTask) => {
      setTasks((prev) => prev.filter((task) => task.id !== deletedTask.id));
    });

    newSocket.on("completedTasksCleared", (clearedTasks) => {
      setTasks((prev) => prev.filter((task) => task.status !== "done"));
    });

    // Pomodoro event handlers
    newSocket.on("pomodoroStateLoaded", (state) => {
      setPomodoroState(state);
    });

    newSocket.on("pomodoroStarted", (state) => {
      setPomodoroState(state);
    });

    newSocket.on("pomodoroPaused", (state) => {
      setPomodoroState(state);
    });

    newSocket.on("pomodoroResumed", (state) => {
      setPomodoroState(state);
    });

    newSocket.on("pomodoroReset", (state) => {
      setPomodoroState(state);
    });

    newSocket.on("pomodoroTick", (state) => {
      setPomodoroState(state);
    });

    newSocket.on("pomodoroWorkCompleted", (data) => {
      setPomodoroState(data.pomodoro);
      console.log("üéâ Work completed:", data.message);
    });

    newSocket.on("pomodoroBreakCompleted", (data) => {
      setPomodoroState(data.pomodoro);
      console.log("‚ú® Break completed:", data.message);
    });

    // Load initial data
    loadInitialData();

    return () => {
      newSocket.close();
    };
  }, []);

  const loadInitialData = async () => {
    try {
      // Load tasks
      const tasksResponse = await fetch("/api/tasks");
      if (tasksResponse.ok) {
        const tasksData = await tasksResponse.json();
        if (tasksData.success) {
          setTasks(tasksData.data);
        }
      }

      // Load pomodoro state
      const pomodoroResponse = await fetch("/api/pomodoro");
      if (pomodoroResponse.ok) {
        const pomodoroData = await pomodoroResponse.json();
        if (pomodoroData.success) {
          setPomodoroState(pomodoroData.data);
        }
      }
    } catch (error) {
      console.error("Failed to load initial data:", error);
    }
  };

  // Pomodoro control functions
  const handlePomodoroStart = async () => {
    try {
      const response = await fetch("/api/pomodoro", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ username: "overlay", duration: 25 }),
      });

      if (!response.ok) {
        console.error("Failed to start pomodoro");
      }
    } catch (error) {
      console.error("Error starting pomodoro:", error);
    }
  };

  const handlePomodoroControl = async (
    action: "pause" | "resume" | "reset"
  ) => {
    try {
      const response = await fetch("/api/pomodoro/control", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ username: "overlay", action }),
      });

      if (!response.ok) {
        console.error(`Failed to ${action} pomodoro`);
      }
    } catch (error) {
      console.error(`Error ${action} pomodoro:`, error);
    }
  };

  const pendingTasks = tasks.filter((task) => task.status === "pending");
  const completedTasks = tasks.filter((task) => task.status === "done");

  return (
    <div className="fixed top-5 right-5 w-[480px] max-h-[90vh] z-50">
      <div
        className="
        bg-gradient-to-br from-gray-900/95 to-black/95 backdrop-blur-lg
        rounded-3xl border-2 border-purple-500/40 shadow-2xl shadow-purple-500/20
        overflow-hidden animate-fade-in
      ">
        {/* Header */}
        <div
          className="
          bg-gradient-to-r from-purple-600 to-purple-700 p-4
          flex justify-between items-center border-b border-purple-500/30
        ">
          <div className="flex items-center gap-3">
            <span className="text-2xl">üìã</span>
            <div>
              <h2 className="text-white font-bold text-lg">Criss Bot</h2>
              <div className="flex items-center gap-4 text-sm">
                <span className="text-purple-200">
                  {pendingTasks.length} pending
                </span>
                {completedTasks.length > 0 && (
                  <span className="text-green-200">
                    {completedTasks.length} done
                  </span>
                )}
              </div>
            </div>
          </div>

          <div className="flex items-center gap-2">
            {/* Connection status */}
            <div
              className={`
              w-3 h-3 rounded-full
              ${
                connectionStatus === "connected"
                  ? "bg-green-400 animate-pulse"
                  : connectionStatus === "connecting"
                  ? "bg-yellow-400 animate-pulse"
                  : "bg-red-400"
              }
            `}
              title={`Connection: ${connectionStatus}`}
            />

            <button
              onClick={() => setShowCompleted(!showCompleted)}
              className="text-white/80 hover:text-white p-2 rounded-lg hover:bg-white/10 transition-colors"
              title={showCompleted ? "Hide completed" : "Show completed"}>
              {showCompleted ? "üëÅÔ∏è" : "üëÅÔ∏è‚Äçüó®Ô∏è"}
            </button>

            <button
              onClick={() => setIsMinimized(!isMinimized)}
              className="text-white/80 hover:text-white p-2 rounded-lg hover:bg-white/10 transition-colors"
              title={isMinimized ? "Expand" : "Minimize"}>
              {isMinimized ? "‚¨ÜÔ∏è" : "‚¨áÔ∏è"}
            </button>
          </div>
        </div>

        {/* Content */}
        {!isMinimized && (
          <div className="p-5 space-y-6 max-h-[70vh] overflow-y-auto">
            {/* Pomodoro Timer */}
            <PomodoroTimer
              state={pomodoroState}
              onStart={handlePomodoroStart}
              onPause={() => handlePomodoroControl("pause")}
              onResume={() => handlePomodoroControl("resume")}
              onReset={() => handlePomodoroControl("reset")}
            />

            {/* Tasks Section */}
            <div>
              <h3 className="text-white font-semibold text-lg mb-4 flex items-center gap-2">
                üîÑ Active Tasks
              </h3>
              <TaskList tasks={pendingTasks} />
            </div>

            {/* Completed Tasks */}
            {completedTasks.length > 0 && showCompleted && (
              <div>
                <div className="h-px bg-gradient-to-r from-transparent via-purple-500/30 to-transparent mb-4" />
                <h3 className="text-white/80 font-semibold text-lg mb-4 flex items-center gap-2">
                  ‚úÖ Recently Completed
                </h3>
                <div className="space-y-2 opacity-80">
                  {completedTasks.slice(-3).map((task, index) => (
                    <div key={task.id} className="text-sm">
                      <TaskItem task={task} index={index} />
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Commands Footer */}
            <div
              className="
              bg-purple-500/10 border border-purple-500/20 rounded-xl p-4
              text-center
            ">
              <h4 className="text-purple-400 font-semibold mb-2">
                üí¨ Chat Commands
              </h4>
              <div className="flex flex-wrap justify-center gap-2 text-xs">
                {[
                  "!add [task]",
                  "!edit [id]",
                  "!done [id]",
                  "!pomodoro [min]",
                  "!pause",
                  "!reset",
                ].map((cmd) => (
                  <code
                    key={cmd}
                    className="
                    bg-purple-500/20 text-purple-300 px-2 py-1 rounded
                    border border-purple-500/30
                  ">
                    {cmd}
                  </code>
                ))}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};
