import React, { useState, useEffect } from "react";
import { io, Socket } from "socket.io-client";

interface PomodoroSession {
  timeLeft: number;
  isActive: boolean;
  mode: "work" | "shortBreak" | "longBreak";
  session: number;
  initiatedBy: string | null;
  nextBreakType: "short" | "long";
}

interface PomodoroTimerProps {
  className?: string;
}

export function PomodoroTimer({ className }: PomodoroTimerProps) {
  const [pomodoroData, setPomodoroData] = useState<PomodoroSession>({
    timeLeft: 1500, // 25 minutes default
    isActive: false,
    mode: "work",
    session: 1,
    initiatedBy: null,
    nextBreakType: "short",
  });

  const [socket, setSocket] = useState<Socket | null>(null);

  useEffect(() => {
    // Initialize socket connection
    const newSocket = io("http://localhost:3001");
    setSocket(newSocket);

    // Listen for Pomodoro updates
    newSocket.on("pomodoroUpdate", (data: PomodoroSession) => {
      setPomodoroData(data);
    });

    // Fetch initial data
    fetchPomodoroStatus();

    // Set up interval to fetch status every second for real-time updates
    const interval = setInterval(() => {
      fetchPomodoroStatus();
    }, 1000);

    return () => {
      newSocket.close();
      clearInterval(interval);
    };
  }, []);

  const fetchPomodoroStatus = async () => {
    try {
      const response = await fetch("/api/pomodoro");
      const data = await response.json();
      if (data.success) {
        setPomodoroData(data.data);
      }
    } catch (error) {
      console.error("Error fetching Pomodoro status:", error);
    }
  };

  const handleAction = async (
    action: "start" | "pause" | "resume" | "reset"
  ) => {
    try {
      if (action === "start") {
        const response = await fetch("/api/pomodoro", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ username: "overlay-user", duration: 25 }),
        });
        const data = await response.json();
        if (data.success) {
          setPomodoroData(data.data);
        }
      } else {
        const response = await fetch("/api/pomodoro/control", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ username: "overlay-user", action }),
        });
        const data = await response.json();
        if (data.success) {
          setPomodoroData(data.data);
        }
      }
    } catch (error) {
      console.error(`Error ${action} Pomodoro:`, error);
    }
  };

  // Format time as MM:SS with seconds
  const formatTime = (totalSeconds: number): string => {
    const minutes = Math.floor(totalSeconds / 60);
    const seconds = totalSeconds % 60;
    return `${minutes}:${seconds.toString().padStart(2, "0")}`;
  };

  // Get progress percentage
  const getProgress = (): number => {
    const totalTime =
      pomodoroData.mode === "work"
        ? 25 * 60 // 25 minutes
        : pomodoroData.mode === "shortBreak"
        ? 10 * 60 // 10 minutes
        : 15 * 60; // 15 minutes for long break

    return ((totalTime - pomodoroData.timeLeft) / totalTime) * 100;
  };

  // Get theme colors based on mode
  const getThemeColors = () => {
    switch (pomodoroData.mode) {
      case "work":
        return {
          primary: "#8a2be2",
          background: "rgba(138, 43, 226, 0.1)",
          text: "#8a2be2",
          emoji: "üçÖ",
        };
      case "shortBreak":
        return {
          primary: "#00ff88",
          background: "rgba(0, 255, 136, 0.1)",
          text: "#00ff88",
          emoji: "‚òï",
        };
      case "longBreak":
        return {
          primary: "#ff6b35",
          background: "rgba(255, 107, 53, 0.1)",
          text: "#ff6b35",
          emoji: "üõãÔ∏è",
        };
      default:
        return {
          primary: "#8a2be2",
          background: "rgba(138, 43, 226, 0.1)",
          text: "#8a2be2",
          emoji: "üçÖ",
        };
    }
  };

  const theme = getThemeColors();
  const progress = getProgress();

  return (
    <div className={`pomodoro-timer ${className}`}>
      <div
        className="pomodoro-container"
        style={{
          background: theme.background,
          border: `2px solid ${theme.primary}`,
          borderRadius: "15px",
          padding: "20px",
          textAlign: "center",
          position: "relative",
          overflow: "hidden",
        }}>
        {/* Animated background glow */}
        <div
          className="pomodoro-glow"
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            background: `linear-gradient(45deg, ${theme.primary}20, transparent, ${theme.primary}20)`,
            animation: "pulse 2s ease-in-out infinite",
            borderRadius: "13px",
          }}
        />

        {/* Content */}
        <div style={{ position: "relative", zIndex: 2 }}>
          {/* Mode and Session */}
          <div
            className="pomodoro-header"
            style={{
              color: theme.text,
              fontSize: "14px",
              fontWeight: "bold",
              marginBottom: "10px",
            }}>
            {theme.emoji}{" "}
            {pomodoroData.mode === "work"
              ? `Work Session ${pomodoroData.session}`
              : pomodoroData.mode === "shortBreak"
              ? "Short Break"
              : "Long Break"}
          </div>

          {/* Time Display with Seconds */}
          <div
            className="pomodoro-time"
            style={{
              fontSize: "28px",
              fontWeight: "bold",
              color: theme.text,
              fontFamily: "monospace",
              marginBottom: "15px",
              textShadow: `0 0 10px ${theme.primary}50`,
            }}>
            {formatTime(pomodoroData.timeLeft)}
          </div>

          {/* Progress Bar */}
          <div
            className="pomodoro-progress-container"
            style={{
              width: "100%",
              height: "8px",
              backgroundColor: "rgba(255, 255, 255, 0.2)",
              borderRadius: "4px",
              marginBottom: "15px",
              overflow: "hidden",
            }}>
            <div
              className="pomodoro-progress-bar"
              style={{
                width: `${progress}%`,
                height: "100%",
                background: `linear-gradient(90deg, ${theme.primary}, ${theme.primary}80)`,
                borderRadius: "4px",
                transition: "width 1s ease",
                boxShadow: `0 0 10px ${theme.primary}60`,
              }}
            />
          </div>

          {/* Controls */}
          <div
            className="pomodoro-controls"
            style={{ display: "flex", gap: "10px", justifyContent: "center" }}>
            {!pomodoroData.isActive ? (
              <button
                onClick={() => handleAction("start")}
                style={{
                  background: theme.primary,
                  color: "white",
                  border: "none",
                  padding: "8px 15px",
                  borderRadius: "5px",
                  cursor: "pointer",
                  fontSize: "12px",
                  fontWeight: "bold",
                }}>
                üöÄ Start
              </button>
            ) : (
              <button
                onClick={() => handleAction("pause")}
                style={{
                  background: "#ff6b35",
                  color: "white",
                  border: "none",
                  padding: "8px 15px",
                  borderRadius: "5px",
                  cursor: "pointer",
                  fontSize: "12px",
                  fontWeight: "bold",
                }}>
                ‚è∏Ô∏è Pause
              </button>
            )}

            <button
              onClick={() => handleAction("reset")}
              style={{
                background: "#6c757d",
                color: "white",
                border: "none",
                padding: "8px 15px",
                borderRadius: "5px",
                cursor: "pointer",
                fontSize: "12px",
                fontWeight: "bold",
              }}>
              üîÑ Reset
            </button>
          </div>

          {/* Status Text */}
          {pomodoroData.isActive && (
            <div
              className="pomodoro-status"
              style={{
                fontSize: "11px",
                color: theme.text,
                marginTop: "10px",
                opacity: 0.8,
              }}>
              {pomodoroData.mode === "work"
                ? `Next: ${
                    pomodoroData.nextBreakType === "short" ? "10-min" : "15-min"
                  } break`
                : `Next: Work session ${pomodoroData.session + 1}`}
            </div>
          )}
        </div>
      </div>

      <style jsx>{`
        @keyframes pulse {
          0%,
          100% {
            opacity: 0.3;
          }
          50% {
            opacity: 0.7;
          }
        }

        .pomodoro-timer button:hover {
          transform: translateY(-1px);
          box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        }

        .pomodoro-timer button:active {
          transform: translateY(0);
        }
      `}</style>
    </div>
  );
}
